<template>

    <div class="agreement-login">

        <div class="h4">Добрый день!</div>

        <p>Для организации {{person.company.ul ? '' : 'ИП '}}{{person.company.name}} необходимо подтверждение Вас как руководителя.</p>

        <div class="agreement-login-btn">
            <button class="btn btn-sm btn-block"
                    target="_blank"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click.prevent="loginClick">Войти через ГОС. Услуги
            </button>
            <p>Для подтверждения вам необходимо авторизоваться через Гос. Услуги</p>
        </div>

    </div>

</template>

<script>
    import {mapState} from "vuex";
    import cookie from "vue-cookies";

    export default {
        name: "ConfirmationMain",
        computed: {
            ...mapState({
                person: state => state.confirmation.person,
                esiaLink: state => state.confirmation.esiaLink
            }),
        },
        data: () => ({
            getParametr: '',
            load: false
        }),
        methods: {
            loginClick() {
                this.load = true
                this.$store.dispatch('signInEsiaAdmin', this.getParametr)
            }
        },
        async mounted() {
            const params = window
                .location
                .search
                .replace('?','')
                .split('&')
                .reduce(
                    function(p,e){
                        const a = e.split('=');
                        p[ decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                        return p;
                    },
                    {}
                );
            this.getParametr = params['c']
            await this.$store.dispatch('clearCookie', this.getParametr)
            this.$store.dispatch('getAdminData', this.getParametr)
        }
    }
</script>

